import React from 'react';
import PropTypes from 'prop-types';

import Avatar from '../Avatar';
import MutedAvatarOverlay from '../Avatar/MutedAvatarOverlay';
import Checkbox from '../Checkbox';
import Label, { LabelStringSet, LabelTypography, LabelColors } from '../Label';

import './index.scss';

const COMPONENT_NAME = 'sendbird-user-list-item';

export default function UserListItem({
  user,
  className,
  checkBox,
  currentUser,
  checked,
  onChange,
  action,
}) {
  const injectingClassNames = Array.isArray(className) ? className : [className];
  const uniqueKey = user.userId;
  const actionRef = React.useRef(null);
  const parentRef = React.useRef(null);
  return (
    <div
      ref={parentRef}
      className={[COMPONENT_NAME, ...injectingClassNames].join(' ')}
    >
      {
        user.isMuted && (
          <MutedAvatarOverlay height={40} width={40} />
        )
      }
      <Avatar
        className={`${COMPONENT_NAME}__avatar`}
        src={user.profileUrl}
        width="40px"
        height="40px"
      />
      <Label
        className={`${COMPONENT_NAME}__title`}
        type={LabelTypography.SUBTITLE_1}
        color={LabelColors.ONBACKGROUND_1}
      >
        {user.nickname || LabelStringSet.NO_NAME}
        {
          (currentUser === user.userId) && (
            ' (You)'
          )
        }
      </Label>
      { // if there is now nickname, display userId
        !user.nickname && (
          <Label
            className={`${COMPONENT_NAME}__subtitle`}
            type={LabelTypography.CAPTION_3}
            color={LabelColors.ONBACKGROUND_2}
          >
            {user.userId}
          </Label>
        )
      }
      {
        checkBox && (
          // eslint-disable-next-line jsx-a11y/label-has-associated-control
          <label
            className={`${COMPONENT_NAME}__checkbox`}
            htmlFor={uniqueKey}
          >
            <Checkbox
              id={uniqueKey}
              checked={checked}
              onChange={(event) => onChange(event)}
            />
          </label>
        )
      }
      {
        user.role === 'operator' && (
          <Label
            className={`${COMPONENT_NAME}__operator`}
            type={LabelTypography.SUBTITLE_2}
            color={LabelColors.ONBACKGROUND_2}
          >
            Operator
          </Label>
        )
      }
      {
        action && (
          <div ref={actionRef} className={`${COMPONENT_NAME}__action`}>
            { action({ actionRef, parentRef }) }
          </div>
        )
      }
    </div>
  );
}

UserListItem.propTypes = {
  user: PropTypes.shape({
    userId: PropTypes.string,
    role: PropTypes.string,
    isMuted: PropTypes.bool,
    nickname: PropTypes.string,
    profileUrl: PropTypes.string,
  }).isRequired,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  currentUser: PropTypes.string,
  action: PropTypes.element,
  checkBox: PropTypes.bool,
  checked: PropTypes.bool,
  onChange: PropTypes.func,
};


UserListItem.defaultProps = {
  className: '',
  currentUser: '',
  checkBox: false,
  checked: false,
  action: null,
  onChange: () => { },
};
